/* eslint-disable*/
import React from "react";
import ReactGA from "react-ga";
import { Col, Row } from "react-materialize";
import ProductItem from "../pages/products/ProductItem";

import DogSlogan from "../../img/backgrounds/dog-slogan.jpg";
// Calha
// Calha
import calhaProd from "../../img/produtos/calha/calha-de-aco-prod.jpg";
import calhaThumb from "../../img/produtos/calha/calha-de-aco.jpg";
// Mesa
import mesaPantograficaProd from "../../img/produtos/mesa-pantografica/mesa-calha-pantografica-prod.jpg";
import mesaPantograficaThumb from "../../img/produtos/mesa-pantografica/mesa-calha-pantografica.jpg";
// Suporte de Soro
import suporteDeSoroProd from "../../img/produtos/suporte-soro-quadripe/suporte-soro-quadripe-prod.jpg";
import suporteDeSoroThumb from "../../img/produtos/suporte-soro-quadripe/suporte-soro-quadripe.jpg";
// Mesa calha
import mesaCalhaInoxProd from "../../img/produtos/mesa-calha/mesa-calha-inox-prod.jpg";
import mesaCalhaInoxThumb from "../../img/produtos/mesa-calha/mesa-calha-inox.jpg";
// Mesa fluidoterapia
import mesaFluidoTerapiaInoxProd from "../../img/produtos/mesa-fluidoterapia/mesa-fluidoterapia-prod.jpg";
import mesaFluidoTerapiaInoxThumb from "../../img/produtos/mesa-fluidoterapia/mesa-fluidoterapia.jpg";
// Bero Verterinrio inox
import bercoVetInoxProd from "../../img/produtos/berco-vet-inox/berco-vet-inox-prod.jpg";
import bercoVetInoxThumb from "../../img/produtos/berco-vet-inox/berco-vet-inox.jpg";

export default class ProductsPageContainer extends React.Component {
  constructor() {
    super();

    this.state = {
      calhaCirurgica: [
        {
          title: "Calha cirrgica - Pequena",
          img: calhaProd,
          modal: calhaProd,
          content:
            "Estrutura em ao inox formato em U, possui suporte lateral para amarrao do animal, calha com inclinao para escoamento de lquido, Medidas: (AxCxL): 16 x 53 x 26 cm."
        }
      ],
      suporteSoro: [
        {
          title: "Suporte para soro quadrip",
          img: suporteDeSoroProd,
          modal: suporteDeSoroProd,
          content:
            "Usado para pendurar bolsas de soro enquanto os pacientes recebem os medicamentos. Ps com ponteiras em borracha, altura regulvel e dois ganchos para armazenar as bolsas de soro ou medicamento, capacidade mnima:  02 frascos e altura Mxima : 1,90m."
        }
      ],
      mesaPantografica: [
        {
          title: "Mesa Pantogrfica",
          img: mesaPantograficaProd,
          modal: mesaPantograficaProd,
          content:
            "Diferenciais: Este tampo permite ao Veterinrio ou Veterinria a regulagem que necessitarem devido ao grande nmero de ajustes e regulagens que esta Mesa  capaz de fazer atrves de suas articulaes e inclinies sendo em sua altura em seu inclinamento para as laterais. Regulagem de altura entre 60 cm a 1,20 cm do cho, acionamento atravs de pedal eltrico, possui 2 sistemas de segurana para proteo de raios e descargas de energia indesejadas."
        }
      ],
      mesaCalha: [
        {
          title: "Mesa Calha Inox",
          img: mesaCalhaInoxProd,
          modal: mesaCalhaInoxProd,
          content:
            "Usada em procedimentos cirrgicos,  Possui tampos de inox de alta resistncia e durabilidade com vincos que escoam os lquidos em direo ao dreno. Altura 1,14m e largura 60cm."
        }
      ],
      mesaFluido: [
        {
          title: "Mesa Fluidoterapia",
          img: mesaFluidoTerapiaInoxProd,
          modal: mesaFluidoTerapiaInoxProd,
          content:
            "Usada em procedimentos cirrgicos, Possui tampos de inox de alta resistncia e durabilidade com vincos que escoam os lquidos em direo ao dreno. Altura 1,14m e largura 60cm." +
            "Possui tampos de inox de alta resistncia e durabilidade com vincos que escoam os lquidos em direo ao dreno. " +
            "Comprimento 1.10m, altura 70cm a 1.00 e largura 60cm. Tampo em forma de cuba com altura de 5 cm e tela superior removvel  em Inox. Ps em tubo quadrado em inox, com manpolas laterais para inclinao e regulagem da altura da mesa. " +
            "Acompanha: suporte de soro em inox com altura regulvel e removvel e balde em alumnio, suporte para amarras e conteno do animal"
        }
      ],
      bercoVet: [
        {
          title: "Bero Para Animais",
          img: bercoVetInoxProd,
          modal: bercoVetInoxProd,
          content:
            "Bero fabricado em Inox com prateleira inferior em inox, gaveta para armazenamento de materiais, suporte de soro removvel, suporte para monitor cirrgico, suporte para cilindro de 5 litros de oxignio 'modelo de ferro', laterais removveis com sistema manual para abaixar e levantar as laterais, rodzios giratrios. "
        }
      ]
    };
  }

  render() {
    const { id } = this.props.match.params;
    ReactGA.pageview(`/produtos/${id}`);
    return (
      <div>
        <Row>
          <Col offset="" s={12} m={12} l={12}>
            <h1>{id}</h1>

            {id === "Calha Cirrgica" ? (
              <ProductItem itens={this.state.calhaCirurgica} />
            ) : (
              ""
            )}
            {id === "Suporte Para Soro" ? (
              <ProductItem itens={this.state.suporteSoro} />
            ) : (
              ""
            )}
            {id === "Mesa Pantogrfica" ? (
              <ProductItem itens={this.state.mesaPantografica} />
            ) : (
              ""
            )}
            {id === "Mesa Calha Inox" ? (
              <ProductItem itens={this.state.mesaCalha} />
            ) : (
              ""
            )}
            {id === "Mesa Fluidoterapia" ? (
              <ProductItem itens={this.state.mesaFluido} />
            ) : (
              ""
            )}
            {id === "Bero Para Animais" ? (
              <ProductItem itens={this.state.bercoVet} />
            ) : (
              ""
            )}
          </Col>
        </Row>
      </div>
    );
  }
}



